ow:0 0 10px rgba(0,0,255,.796);border-radius:16px}.CreateEvent .noEvents{color:gray;margin-top:10px}.writeEventName{width:90%;height:105px;overflow:hidden;margin:0 auto;margin-top:40px}.writeEventName>textarea{width:100%;height:80%;overflow:hidden;resize:none;border:1px solid rgba(166,167,169,.57);box-sizing:border-box;border-radius:5px;font-size:16px}.writeEventName>textarea:focus{border:none;outline:none;border:2px solid #2323f2}.wraperDateEvent{margin:20px auto 0;height:25px;display:flex;justify-content:center;align-items:center;column-gap:25px}.wraperDateEvent .dateEvent{padding-left:30px;background:url("../../icons/icons8-calendar-64.png");background-size:contain;background-repeat:no-repeat;cursor:pointer}.wraperDateEvent .dateEvent:hover{background:url("../../icons/icons8-calendar-96 (1).png");background-size:contain;background-repeat:no-repeat;outline:none}.wraperDateEvent .timeEvent>input{padding:2px 5px;font-size:15px;border:none;border-radius:5px;background-color:rgba(211,211,211,.692)}.wraperDateEvent .timeEvent>input:hover,.wraperDateEvent .timeEvent>input:focus{background-repeat:no-repeat;outline:none}.saveEvent{margin:30px 10px 10px 225px;height:30px;background-color:#1769e4;color:#fff;border:none;border-radius:5px;font-size:16px;padding:5px 10px;cursor:pointer}.saveEvent:hover{background-color:blue}.confirmSave{width:20px;height:20px;background:url("../../icons/icons8-галочка-48.png");background-size:contain;background-repeat:no-repeat;background-position:center}.confirmSave:hover{background:url("../../icons/icons8-ок-48.png");background-size:contain;background-repeat:no-repeat;background-position:center}.cancel{width:20px;height:20px;background:url("../../icons/icons8-удалить-48.png");background-size:contain;background-repeat:no-repeat;background-position:center}.cancel:hover{background:url("../../icons/icons8-отмена-48.png");background-size:contain;background-repeat:no-repeat;background-position:center}.errorUserMessage{height:50px;top:-5px;border-r