$widthCalendar:300; 
$heightCalendar:320;


.calendarDays{
    width: $widthCalendar+px;
    max-height: $heightCalendar+px;
    margin: 0 auto;
    box-shadow: 8px 3px 22px 10px rgba(150, 150, 150, 0.11);
    border-radius: 16px;
    padding: 24px;
    
    
    
    >.weekDays{
        margin-top: 16px;
        width: 100%;
        display: grid;
        grid-template-columns: repeat(7,1fr);
    }
    >.monthDays{
        margin-top: 16px;
        display: grid;
        grid-template-columns: repeat(7,1fr);
        grid-auto-rows: 1fr;
        row-gap: 16px;
        cursor: pointer;
 
    }
}

.year-month{
    cursor:pointer;
    font-weight: 600;
    font-size: 20px;
}
.line{
    border-bottom: 0.8px solid #E4E5E7;
    width: 100%;
}
.calendarHeader{
    display: flex;
    justify-content: space-between;
    align-items:center;
    height: 40px;
}
.today{
    text-align: left;
    font-size: 14px;
    color:black;
    cursor: pointer;
    &:hover{
        color: gray;
    }
}
.colorGray{
    color:rgba(128, 128, 128, 0.875);
}
.btn-leafCalendar{
    text-align: center;
    border-radius: 5px;
    background-color:white;
    border: none;
    color: #848A95;
    margin-left: 20px;
    cursor: pointer;
    font-size: 16px;
    &:hover{
        color: blue;
    }
}
.day{
    width: 100%;
    border-radius: 20px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    >div{
        &:hover{
            outline:1px solid rgba(0, 0, 0, 0.273);
        }
    }
    
}
.pointEvent{
    margin-top: 1px;
    height: 4px;
    width: 4px;
    background-color:rgba(0, 0, 255, 0.418);
    // background-color:rgba(255, 99, 71, 0.458);
    border-radius: 50%;
}
.noPointEvent{
    margin-top: 1px;
    height: 4px;
    width: 4px;
    background: none;
}
.choosMonth{
    // height: $heightCalendar+ px;
    width: $widthCalendar+px;
    margin: 0 auto;
    box-shadow: 8px 3px 22px 10px rgba(150, 150, 150, 0.11);
    border-radius: 16px;
    padding: 24px;
    >.months{
        margin-top: 16px;
        display: grid;
        grid-template-columns: repeat(4,60px);
        grid-template-rows: repeat(4, 50px);
        justify-content: space-around;
        row-gap: 5px;
        >div{
            border-radius: 20px;
            text-align: center;
            display: grid;
            align-content: center;
            cursor: pointer;
            overflow: hidden;
            font-weight: bolder;
            box-sizing: border-box;
            &:hover{
                outline:1px solid rgba(0, 0, 0, 0.273);
            }
        }
    }
}
.isActive{
    background-color: blue;
    color: white;
}
